import React, { useState, useEffect, useContext } from "react";
import { SpriteContext } from "../context/SpriteContext";
import { InputGroup, FormGroup } from "@blueprintjs/core";
import image1 from "../sprites/1.png";
import image2 from "../sprites/2.png";
import image3 from "../sprites/3.png";
import image4 from "../sprites/4.png";
import image5 from "../sprites/5.png";
import image6 from "../sprites/6.png";
import image7 from "../sprites/7.png";
import image8 from "../sprites/8.png";
import image9 from "../sprites/9.png";
import image10 from "../sprites/10.png";
import abnb from "../sprites/abnb.png";
import ApeBasicNeutral from "../sprites/ApeBasicNeutral.png";
import aquenbe from "../sprites/aquenbe.png";
import bbnb from "../sprites/bbnb.png";
import BeeBasiNeutral from "../sprites/BeeBasiNeutral.png";
import GreenD from "../sprites/GreenD.png";
import hbnb from "../sprites/hbnb.png";
import humanBasicNeutral from "../sprites/humanBasicNeutral.png";
import Monogo1 from "../sprites/Monogo1.png";
import monongo2png from "../sprites/monongo2png.png";
import wbnb from "../sprites/wbnb.png";
type SpritesHandlerProps = {
  SquareSize: number;
  NumberColumns: number;
  NumberRows: number;
  Show: boolean;
};

const imageArray = [
  image1,
  image2,
  image3,
  image4,
  image5,
  image6,
  image7,
  image8,
  image9,
  image10,
  abnb,
  ApeBasicNeutral,
  aquenbe,
  bbnb,
  BeeBasiNeutral,
  GreenD,
  hbnb,
  humanBasicNeutral,
  Monogo1,
  monongo2png,
  wbnb,
];
const SpritesHandler = ({
  SquareSize,
  NumberColumns,
  NumberRows,
  Show,
}: SpritesHandlerProps) => {
  const { setSprite } = useContext(SpriteContext);
  // const [show, setShow] = useState(false);
  const [reRender, setReRender] = useState(true);
  const [sprites, setSprites] = useState([]);

  useEffect(() => {
    let preparingSprites = [];
    for (const image of imageArray) {
      preparingSprites.push({
        content: image,
        height: SquareSize,
        width: SquareSize,
        verticalMultiplier: 1,
        horizontalMultiplier: 1,
        color: "transparent",
      });
    }
    setSprites(preparingSprites);
  }, [SquareSize]);

  const spriteCliked = (index: number) => {
    for (const sprite of sprites) {
      sprite.color = "transparent";
    }
    sprites[index].color = "white";
    setSprite({
      content: sprites[index].content,
      horizontalMultiplier: sprites[index].horizontalMultiplier,
      verticalMultiplier: sprites[index].verticalMultiplier,
      preview: true,
      type: "neutral",
    });
  };

  return (
    <>
      {Show && (
        <div className="Sprites-container">
          {sprites.length === 0 ? (
            <></>
          ) : (
            <>
              {" "}
              {sprites.map((sprite, index) => (
                <>
                  <div
                    className="Sprite-details"
                    onClick={(e) => spriteCliked(index)}
                  >
                    <img
                      style={{
                        height: `${sprite.height}em`,
                        width: `${sprite.width}em`,
                        backgroundColor: `${sprite.color}`,
                      }}
                      key={89 + 1 * index}
                      className="sprite"
                      src={sprite.content}
                      alt="sprite"
                      tabIndex={index}
                    />
                    <div className="sprite-inputs">
                      <FormGroup label="Rows">
                        <InputGroup
                          className="sprite-size"
                          intent={"primary"}
                          type="number"
                          defaultValue="1"
                          min="1"
                          max={NumberRows}
                          onChange={(
                            e: React.ChangeEvent<HTMLInputElement>
                          ) => {
                            if (
                              e.target.valueAsNumber < 1 ||
                              e.target.valueAsNumber > NumberRows
                            )
                              return;
                            sprites[index].horizontalMultiplier =
                              e.target.valueAsNumber;
                            for (const sprite of sprites) {
                              sprite.color = "transparent";
                            }
                            sprites[index].color = "white";
                            setReRender(!reRender);
                            setSprite({
                              content: sprite.content,
                              horizontalMultiplier: e.target.valueAsNumber,
                              verticalMultiplier:
                                sprites[index].verticalMultiplier,
                              preview: true,
                              type: "neutral",
                            });
                          }}
                        />
                      </FormGroup>

                      <FormGroup label="Columns">
                        <InputGroup
                          className="sprite-size"
                          intent={"primary"}
                          type="number"
                          defaultValue="1"
                          min="1"
                          max={NumberColumns}
                          onChange={(
                            e: React.ChangeEvent<HTMLInputElement>
                          ) => {
                            if (
                              e.target.valueAsNumber < 1 ||
                              e.target.valueAsNumber > NumberColumns
                            )
                              return;
                            sprites[index].verticalMultiplier =
                              e.target.valueAsNumber;
                            for (const sprite of sprites) {
                              sprite.color = "transparent";
                            }
                            sprites[index].color = "white";
                            setReRender(!reRender);
                            setSprite({
                              content: sprite.content,
                              horizontalMultiplier:
                                sprites[index].horizontalMultiplier,
                              verticalMultiplier: e.target.valueAsNumber,
                              preview: true,
                              type: "neutral",
                            });
                          }}
                        />
                      </FormGroup>
                    </div>
                  </div>
                </>
              ))}
            </>
          )}
        </div>
      )}{" "}
    </>
  );
};
export default SpritesHandler;
