import React from "react";
type SquareProps = {
  Class: string;
  Height: string;
  Width: string;
  Coordinates: Array<number>;
  Color: string;
  onClick: any;
  onMouseEnter: any;
};

export const Square = ({
  Class,
  Height,
  Width,
  Coordinates,
  Color,
  onClick,
  onMouseEnter,
}: SquareProps) => {
  return (
    <div
      className={Class}
      style={{
        height: Height,
        width: Width,
        gridArea: `${Coordinates[0]} / ${Coordinates[1]}`,
        backgroundColor: Color,
      }}
      onClick={onClick}
      onMouseEnter={onMouseEnter}
    >
      {""}
    </div>
  );
};
